.time-picker {

    &__wrap {
        width: 100%;

        @include respond-min(md) {
            max-height: 276px;
            overflow: auto;
            border: 1px solid $color-border--lightest;
        }

        &.is-empty {
            border-left: 0;
            border-right: 0;
            border-bottom: 0;

            .time-picker {

                &__empty {
                    display: flex !important;
                }
            }
        }
    } //.time-picker__wrap

    &__item {
        width: 100%;
        padding: 15px 20px;
        color: $color-text--darkest;
        cursor: pointer;
        position: relative;
        @include flex;
        justify-content: center;

        &:hover {
            @include respond-min(md) {
                box-shadow: inset 0 0 0 2px $color-main;
                border-color: transparent;
            }
        }

        &::after {
            content: '\f105';
            @include fa;
            font-size: 20px;
            color: $color-main;
            @include centre('y');
            right: 18px;

            @include respond-min(md) {
                display: none;
            }
        }

        & + .time-picker__item {
            border-top: 1px solid $color-border--lightest;
        }

        &.is-disabled {
            color: $gray--lighten;

            &:hover {
                box-shadow: none;
            }

            .time-picker__title {
                padding-right: 6px;
                color: $color-text--lighten;
            }

            &::after {
                display: none;
            }
        }

        &.is-active {
            box-shadow: inset 0 0 0 2px $color-main;
            border-color: transparent;
        }
    } //.time-picker__item

    &__empty {
        width: 100%;
        height: 274px;
        font-family: $f;
        color: $color-text;
        display: none !important;
        align-items: center;
        justify-content: center;
    } //.time-picker__empty

    a {
        transition: none;
    }
} //.time-picker
