.order {
    width: 100%;

    &:not(.order--info) {
        padding-bottom: 30px;

        @include respond-to(sm) {
            padding-bottom: 0;
            overflow: hidden;
        }
    }

    &__inner {
        @extend .container;
        position: relative;
        display: flex;
        justify-content: center;

        @include respond-to(xs) {
            padding-right: 0;
            padding-left: 0;
        }

        @include respond-min(md) {
            justify-content: space-between;
        }
    } //.order__inner

    &__title {
        padding-right: 20px;
        font-family: $f;
        color: #fff;
        @include flexCenter;
        justify-content: center;
        flex: 1;

        @include respond-min(md) {
            font-size: 20px;
            color: $gray--darkest;
        }
    } //.order__title

    &__top-line {
        min-height: 50px;
        padding: 6px 20px;
        position: fixed;
        top: 0;
        width: 100%;
        background-color: $color-bg--dark;
        z-index: 110;
        @include flexCenter;

        @include respond-min(md) {
            padding: 11.5px 20px;
            margin-bottom: 30px;
            background-color: #fff;
            border-bottom: 1px solid $color-border--lightest;
            position: relative;
        }

        .btn-link {
            @include centre('y');
            left: 0;

            &:hover {
                text-decoration: none;
            }

            i {
                margin-top: 0;
                font-size: 20px;
            }

            span {
                @include respond-to(xs) {
                    display: none;
                }
            }
        } //.order__top-line .btn-link
    } //.order__top-line

    &__left {
        width: 100%;

        @include respond-min(md) {
            @include flexColumn;
            width: 65%;
            margin-right: 30px;
        }

        &_top {
            position: absolute;
            width: 100%;
            top: 0;
            left: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 10px;
            background-color: #fff;
            border-bottom: 1px solid $color-border--lightest;
            z-index: 20;

            @include respond-min(md) {
                display: flex;
                position: relative;
                width: 100%;
                padding: 0;
                margin: 0;
                margin-bottom: 30px;
                border-bottom: none;
            }
        } //.order__left_top
    } //.order__left

    &__right {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        background-color: #fff;

        @include respond-min(md) {
            position: relative;
            top: 0;
            width: 32%;
        }
    } //.order__right

    &--info {
        padding-top: 0;

        @include respond-min(md) {
            padding-top: 30px;
        }
    } //.order--info

    .btn-icon {
        padding: 0;
        background-color: transparent;

        &:hover {
            cursor: pointer;
        } //.icon:hover

        &--delete {
            width: 20px;
            height: 20px;
            overflow: hidden;
            position: absolute;
            left: 70px;
            top: 50%;
            transform: translateY(-50%);
            @include tr;

            .fa-minus-circle {
                font-size: 20px;
                color: $color-main;
            }
        } //.icon--delete
    } //.order .icon

    .modal {

        &-dialog {
            display: flex;
            justify-content: center;
        } //.order .modal-dialog

        &-header {
            align-items: center;

            .btn {
                margin-left: auto;
                font-family: $f-light;
            }
        }

        &-content {
            @include respond-min(md) {
                max-width: 330px;
            }
        } //.order .modal-content

        &-title {
            font-family: $f-medium;
        } //.order .modal-title

        &-body {

            &__scroll-wrap {
                max-height: 420px;
                overflow-y: auto;
                @include bb-scroll--thin;
            } //.order .modal-body__scroll-wrap
        }

        &-footer {
            margin-top: auto;
            background-color: transparent;
            display: flex;
            justify-content: center;
            flex-shrink: 0;

            .btn {
                padding: 3px 35px;
                font-size: 15px;
            }
        } //.order .modal-footer

        .btn-link {
            padding: 0;
        }

    } //.order .modal

    .bb-checkbox {

        &__box {
            position: relative;
            margin-bottom: 5px;
        }

        &--master {
            display: flex;
            align-items: center;
        }//.order .bb-checkbox--master
    } //.order .bb-checkbox
} //.order

.master-picker {
    width: 100%;
    padding: 15px 17px;
    border-bottom: 1px solid $color-border--lightest;
    @include flexCenter;

    @include respond-min(md) {
        margin-bottom: 30px;
        border: 1px solid $color-border--lightest;
    }

    &:hover {
        cursor: default;
    }

    &__item {
        height: 70px;
        padding: 10px 15px;
        position: relative;
        @include flexCenter;

        & + & {
            border-top: 1px solid $color-border--lightest;
        } //.modal-box__item + .modal-box__item

        .user {
            margin-right: auto;
            @include flexCenter;

            &__img {
                width: 40px;
                height: 40px;
            } //.user__img

            &__avatar {
                margin-bottom: 0;
                margin-right: 13px;
            } //.user__avatar
        } //.user

        .bb-checkbox {
            flex: 1;

            &__box {
                margin: 0;
            }
        } //.master-picker__item .bb-checkbox__box
    } //.master-picker__item

    &__title {
        display: flex;
        flex-direction: column;
        justify-content: center;

        &_top {
            font-size: 15px;
            line-height: 1.4;
        } //.master-picker__title_top

        &_bottom {
            font-family: $f-i;
            font-size: 14px;
            color: $color-text--light;
        } //.master-picker__title_bottom
    } //.master-picker__title

    &__btn {
        margin-left: auto;
        padding: 10px 0 10px 10px;
        font-family: $f-light;
        font-size: 15px;
    } //.master-picker__btn

    .user {
        line-height: 1;
        margin-right: 20px;

        &__avatar {
            margin-bottom: 0;
        } //.user__avatar

        &__img {
            width: 35px;
            height: 35px;
        } //.user__img
    } //.user
} //.master-picker

.confirm-msg {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px;
    font-size: 15px;
    background-color: #f4f9f2;
    border: 1px solid #d7eacf;

    @include respond-min(md) {
        margin: 0;
        width: 100%;
    }

    &:hover {
        cursor: default;
    }

    &__icon {
        margin: 0 20px 0 10px;

        i {
            font-size: 40px;
            color: #6fb557;
        }
    } //.confirm-msg__icon

    &__title {
        font-family: $f-medium;

        @include respond-min(md) {
            margin-bottom: 5px;
        }
    } //.confirm-msg__title
} //.confirm-msg

.user-info {
    width: 100%;
    @extend %boxLight;

    @include respond-to(md) {
        box-shadow: none;
        border: 0;
    }

    &__inner {
        width: 100%;
        background-color: #fff;
        transition: margin-left 0.5s;
        z-index: 10;
    } //.user-info__inner

    &__wrap-scroll {
        max-height: 400px;
        overflow: auto;
    }

    &__top {
        padding: 20px;
        border-bottom: 1px solid $color-border--lightest;
        overflow: hidden;
        position: relative;
        @include tr;
        @include flexColumn;

        &.is-active {

            &:hover {
                cursor: default;
            }

            .user-info {

                &__date {
                    opacity: 1;
                    visibility: visible;
                    top: 50px;
                    @include tr;
                } //.user-info__date

                &__title {
                    font-family: $f-i;
                    color: $color-text--light;
                    font-size: 12px;
                    @include tr;
                } //.user-info__title

                &__header {
                    margin-bottom: 27px;
                } //.user-info__user
            } //.user-info
        } //.user-info__top.is-active

        .user-info__title {
            font-family: $f-medium;
            font-size: 17px;
            color: $color-text;
        } //.user-info__top .user-info__title
    } //.user-info__top

    &__bottom {
        display: flex;
        justify-content: space-between;
        position: absolute;
        bottom: 0;
        right: -100%;
        width: 100%;
        @include tr;
        background-color: $color-bg--light;

        @include respond-min(md) {
            position: relative;
            right: 0;
            padding: 10px;
            background-color: transparent;
        }

        .user-info__title {
            font-family: $f-medium;
            text-transform: uppercase;
        } //.user-info__title

        &.is-active {
            right: 0;
            @include tr;

            @include respond-min(md) {
                padding: 10px;
            }
        }
    } //.user-info__bottom

    &__item {
        width: 100%;
        padding: 12.5px 35px;
        position: relative;
        display: flex;
        align-items: center;

        @include respond-min(md) {
            padding: 20px;
        }

        &:hover {
            cursor: default;
        }

        &.has-hover {

            &:hover {
                background-color: $color-bg--hover;
                cursor: default;

                .user-info__inner {
                    margin-left: 32px;
                    transition: margin-left 0.5s;
                    background-color: $color-bg--lightest;
                }

                .btn-icon {
                    left: 20px;
                }
            }
        } //.service-list__item.is-hover

        & + .user-info__item {
            border-top: 1px solid $color-border--lightest;
        }
    } //.user-info__item

    &__header {
        @include flexCenter;

        .user {
            @include flexCenter;

            &__img {
                width: 40px;
                height: 40px;
                margin-right: 15px;
            }

            &__title {
                font-family: $f-medium;
                font-size: 16px;
                color: $color-text;
            }
        } //.user-info__title
    } //.user-info__header

    &__details {
        display: flex;
        align-items: center;
        font-size: 14px;
    } //.user-info__details

    &__left {
        width: 70%;
        font-family: $f-i;
        color: $color-text--light;
        font-size: 14px;
    } //.user-info__left

    &__right {
        font-family: $f-bold;
        font-size: 16px;
        color: $gray--darken;
        margin-left: auto;

        &--total {
            margin-left: auto;
            font-family: $f-medium;
            font-size: 18px;
        } //.user-info__right--price
    } //.user-info__right

    &__user {
        display: flex;
        align-items: center;

        .user {

            &__avatar {
                margin-bottom: 0;
                margin-right: 20px;
            } //.user__avatar

            &__img {
                width: 45px;
                height: 45px;
            } //.user__img
        }
    } //.user-info__user

    &__title {
        font-family: $f-light;
        color: $color-main;
    } //.user-info__title

    &__date {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: -50%;
    } //.user-info__date

    &__note {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 11.5px 20px;

        @include respond-min(md) {
            padding: 10px;
        }
    } //.user-info__desc

    &__btn {
        display: none;
        width: 100%;
        height: 50px;
        line-height: 51px;
        padding: 0 25px;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        border-radius: 0;
        position: relative;
        justify-content: space-between;

        @include respond-min(md) {
            top: -100px;
            width: 100%;
            z-index: -1;
            border-radius: $border-radius;
            overflow: hidden;
        }

        @include respond-to(sm) {
            box-shadow: none;
        }

        @include respond-to(xs) {
            padding: 0 25px;
        }

        &.is-active {
            display: flex;
            z-index: 1;
            top: 0;
            @include tr;
        }
    } //.user-info__btn

    &--confirmed {

        .user-info__title {
            color: $color-text;
        } //.user-info__title
    } //.user-info--confirmed

    &.is-fixed {
        position: fixed;
        top: 0;
        width: 384px;
    } //.user-info.is-fixed

    .fa-ruble-sign {
        margin-left: 5px;
        font-size: 14px;
        color: $color-icon;
    } //.fa-ruble-sign
} //.user-info

.info-box {
    width: 100%;
    padding: 20px;
    margin-bottom: 30px;
    @extend %boxLight;

    &:hover {
        cursor: default;
    }

    &__title {
        margin-bottom: 8px;
        font-family: $f;
        font-size: 18px;
        color: $gray--darkest;
    } //.service-box__title

    &__desc {
        font-family: $f-light;
        font-size: 15px;
    } //.info-box__desc
} //.info-box

.service-list {
    width: 100%;
    background-color: #fff;
    @include tr;
    @include flexColumn;

    &:hover {
        cursor: default;
    }

    &__title {
        margin-bottom: 20px;
        font-family: $f-medium;
        font-size: 18px;
        @include flexCenter;
        flex-shrink: 0;

        @include respond-to(sm) {
            margin-bottom: 0;
            padding: 10px 20px 9px;
            border-bottom: 1px solid $color-border--lightest;
        }

        .btn {
            margin-left: auto;
            font-family: $f-light;
        }
    } //.service-list__title

    &__item {
        position: relative;
        width: 100%;
        padding: 15px 50px 15px 20px;
        // border-bottom: 1px solid $color-border--lightest;
        @extend %boxLight;

        &:last-child {
            border-bottom: 0;
        }

        @include respond-to(md) {
            box-shadow: none;
            border-top: 0;
            border-left: 0;
            border-right: 0;
        }

        @include respond-min(md) {
            // box-shadow: $bxs;
            // border-radius: $border-radius;
        }

        & + & {
            @include respond-min(md) {
                margin-top: 20px;
            }
        }
    } //.service-list__item

    &__icon {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);

        i {
            @include respond-min(md) {
                font-size: 15px;
            }
        }

        &.fa-info-circle {
            left: -0.5px;
        }

        &.fa-map-marker-alt {
            left: 1px;
        }

        &.fa-clock {
            left: -0.5px;
        }

        &.fa-ruble-sign {
            font-size: 15px;
            left: 1px;
        }
    } //.service-list__icon

    &__desc {
        margin-left: 30px;

        @include respond-min(md) {
            font-size: 14px;
        }
    } //.service-list__desc

    &__edit {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        right: 0;
        color: $color-main;
        width: 50px;
        height: 50px;

        @include respond-min(md) {
            display: none;
        }
    } //.service-list__edit

    &__row {
        position: relative;
        display: flex;
        align-items: center;

        & + & {
            margin-top: 10px;
        }
    } //.service-list__row

    &__btn {
        width: auto;
        margin-top: 30px;
        align-self: flex-end;

        @include respond-to(sm) {
            width: 100%;
            border-radius: 0;
            box-shadow: none;
            position: fixed;
            bottom: 60px;
        }

        &:hover {
            @include respond-to(sm) {
                box-shadow: none;
                transform: none;
            }
        }
    } //.service-list__btn

    &--mobile {
        position: fixed;
        top: 50px;
        right: -100%;
        width: 100%;
        bottom: 50px;
        background-color: #fff;
        overflow-y: auto;
    } //.service-list--mobile

    &.is-active {
        right: 0;
        z-index: z(dropdown);
    }

    &--fixed {
        @include respond-to(md) {
            top: 153px;
        }

        @include respond-to(sm) {
            position: fixed;
            top: 143px;
            right: 0;
            width: 100%;
            bottom: -1px;
            overflow-y: auto;
        }

        @include respond-to(xs) {
            top: 133px;
        }

        .service-list {

            &__inner {
                @include respond-to(sm) {
                    overflow-y: auto;
                    position: absolute;
                    top: 48px;
                    left: 0;
                    right: 0;
                    bottom: 50px;
                }

                @include respond-to(xs) {
                    top: 58px;
                }
            } //.service-list__inner
        }
    } //.service-list--fixed
} //.service-list
