.day-picker {
    width: 100%;
    padding-bottom: 18px;
    background-color: #fff;
    border-bottom: 1px solid $color-border--lightest;

    @include respond-min(md) {
        padding-bottom: 0;
        margin-bottom: 20px;
        border-bottom: 0;
    }

    &__top {
        padding: 15px;
        margin-bottom: 10px;
        text-align: center;
        color: #000;

        &:hover {
            cursor: default;
        }
    } //.day-picker__top

    &__carousel {
        position: relative;
        display: flex;
        align-items: center;
    } //.day-picker__carousel

    &__item {
        width: 14%;
        text-align: center;
        cursor: default;

        @include respond-min(md) {
            width: 60px;
            margin: 0 12px;
        }

        &:hover:not(.is-active):not(.is-disabled) {
            cursor: pointer;

            .day-picker__day_num {
                background-color: darken(#fff, 3%);
            }
        }

        &.is-active {

            .day-picker__day_num {
                background-color: #000;
                color: #fff;
            }
        }

        &.is-disabled {

            .day-picker__day {

                &_title {
                    color: $color-text--lighten;
                }

                &_num {
                    color: $color-text--lighten;
                }
            }
        }
    } //.day-picker__item

    &__day {

        &_title {
            margin-bottom: 3px;
            text-transform: uppercase;
            font-family: $f;
            font-size: 11px;
        } //.day-picker__day_title

        &_num {
            margin: 0 auto;
            width: 40px;
            height: 40px;
            font-size: 22px;
            color: #000;
            border-radius: 50%;
            transition: 0.2s;
            display: flex;
            justify-content: center;
            align-items: center;
        } //.day-picker__day_num
    } //.day-picker__day

    &__week {
        font-family: $f-light;
    } //.day-picker__week

    &__arrow {
        border: 0;
        outline: none;
        position: absolute;
        top: -83%;
        color: $color-main;
        font-size: 20px;
        background-color: transparent;

        @include respond-min(md) {
            width: 45px;
            height: 45px;
            border-radius: 50%;
            background-color: #fff;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        i {
            font-weight: 500;
            padding-top: 2px;
        }

        &--left {
            left: 13px;

            i {
                padding-right: 3px;
            }
        } //.day-picker__arrow--left

        &--right {
            right: 13px;

            i {
                padding-left: 3px;
            }
        } //.day-picker__arrow--right

        &:hover:not(.is-disabled) {
            @include respond-min(md) {
                background-color: $color-main;
                color: #fff;
                @include tr;
                cursor: pointer;
            }
        }

        &.is-disabled {
            color: $color-text--light;
            cursor: default;
        }
    } //.day-picker__arrow

    &__list {
        width: 100%;
        flex: 1;
        display: flex;
        justify-content: center;
    } //.day-picker__list

    &.is-fixed {
        box-shadow: $bxs;
        position: fixed;
        top: 50px;
        left: 0;
        width: 100%;
        z-index: z(fixed);
    }
} //.day-picker
