.page-home {

    .header-action {
        margin-left: auto;

        &__search {
            display: none;
        }
    }

    .nav-dropdown {
        box-shadow: 3px 20px 20px rgba(0, 0, 0, 0.4);
    }

    .search__group--services {
        display: none;
    }

    .button-icon--info {

        &::before {
            color: $color-text;
        }
    }

    .firstscreen {
        @include respond-to(xs) {
            margin-bottom: 0;
        }
    }
} //.page-home

.firstscreen {
    padding: 0;

    &__inner {
        max-width: 1200px;
        min-height: 400px;
        margin: auto;
        @include cover;
        @include sectionOverlay();
        @include flex;
        @include respond-to(sm) {
            flex-wrap: wrap;
        }
    } //.firstscreen__inner

    &__left {
        padding: 0 50px;
        position: relative;
        box-sizing: content-box;
        @include flexColumn;
        align-self: center;

        @include respond-to(sm) {
            padding: 0 20px 50px;
            z-index: 2;
        }
    } //.firstscreen__left

    &__right {
        text-align: center;
        @include flexColumn;
        justify-content: center;
        align-items: center;

        @include respond-to(sm) {
            width: 100%;
            background-color: $color-bg--lightest;

            .offer {
                background-color: transparent;
            }
        }

        @include respond-to(xs) {
            padding: 40px 0;
            background-color: #fff;
        }
    } //.firstscreen__right

    &__search {
        margin: 0 auto 60px 0;

        @include respond-to(xs) {
            margin-bottom: 40px;
        }

        .search {
            margin-bottom: 0;

            &__hint {
                left: 0;
                right: 0;
                border-radius: $border-radius;
            }

            i {
                display: none;

                &.fa-search {
                    display: none !important;
                }
            }
        }
    } //.firstscreen__search

    &__title {
        display: block;
        max-width: 600px;
        width: auto;
        margin: 97px 0 15px;
        font-size: 35px;
        color: #fff;
        @include textShadow;
        line-height: 1.2;
        @include respond-to(xs) {
            margin: 80px 0 10px;
            text-align: center;
            font-size: 30px;
        }
    } //.firstscreen__title

    &__subtitle {
        display: block;
        max-width: 585px;
        margin-bottom: 30px;
        font-family: $f-light;
        font-size: 18px;
        color: #fff;
        @include textShadow;
        @include respond-to(xs) {
            margin-bottom: 30px;
            text-align: center;
        }
    } //.firstscreen__subtitle
} //.firstscreen

.offer {
    max-width: 360px;
    width: 100%;
    padding: 30px 30px 25px;
    color: #000;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: $border-radius;

    @include respond-to(xs) {
        max-width: 100%;
        padding: 0 20px;
    }

    &__title {
        display: block;
        margin-bottom: 20px;
        font-family: $f-bold;
        font-size: 30px;
        line-height: 1.2;

        @include respond-to(xs) {
            font-size: 28px;
            text-align: center;
        }
    } //.offer__title

    &__desc {
        display: block;
        margin-bottom: 25px;
        font-family: $f-light;
        font-size: 17px;

        @include respond-to(xs) {
            text-align: center;
        }
    } //.offer__desc

    &__note {
        display: block;
        width: 100%;
        margin-top: 5px;
        text-align: center;
        font-size: 14px;
        color: $color-text--light;
    } //.offer__note

    &__btn {
        width: 100%;
    } //.offer__btn
} //.offer

.bb-category {
    padding: 0;

    &__items {
        @include flexColumn;
    } //.category__items

    &__item {
        height: 200px;
        margin-bottom: 1px;
        position: relative;
        @include cover;

        &:nth-child(even) {
            background-color: $color-accent;

            .bb-category__title {
                left: 10%;
            }
        }

        &:nth-child(odd) {
            background-color: $color-primary--light;

            .bb-category__title {
                right: 10%;
            }
        }

        @include respond-to(xs) {
            margin-right: 0;
            margin-bottom: 1px;
            border-radius: 0;
        }
    } //.category__item

    &__title {
        text-transform: uppercase;
        font-family: roboto-600;
        font-size: 17px;
        letter-spacing: 0.5px;
        color: #fff;
        @include vy;
    } //.category__title
} //.bb-category

.about {

    .text-block {
        margin-bottom: 0;

        &__item:last-child {
            @include respond-to(xs) {
                margin-bottom: 0;
            }
        }
    }
} //.about

.description {

    &__inner {
        display: flex;
        max-width: 950px;
        margin: auto;

        @include respond-to(xs) {
            flex-direction: column;
            padding: 0 10px;
            text-align: center;
        }
    } //.desc__inner

    &__title {
        margin-bottom: 30px;
        font-family: $f-bold;
        font-size: 25px;
        color: #000;

        @include respond-to(xs) {
            line-height: 1.2;
        }
    } //.desc__title

    &__left {
        @include flexColumn;
        justify-content: center;
        margin-right: 100px;

        @include respond-to(xs) {
            margin-right: 0;
            margin-bottom: 50px;
        }
    } //.desc__left

    &__right {

        img {
            @include respond-to(xs) {
                width: 60%;
                margin: auto;
            }
        }
    } //.desc__right
} //.description

.blog {
    margin-bottom: 0;

    @include respond-to(xs) {
        padding: 0;
        margin: 0;
    }

    &--manicure {
        background: url('../img/general/bg-pattern-blue.svg'), #f3f6ff;
        background-size: 125px;
    }

    &__inner {
        @include row-flex();
        @include md(justify-content, center);
    } //.blog__inner

    &__post {
        @include col();
        @include size(3);
        @include size-sm(6);
        @include size-i5(12);
        margin-bottom: $gutter * 2;
    } //.blog__post

    .bb-title {
        @include respond-to(xs) {
            display: none;
        }
    }
} //.blog

.blog-item {
    max-width: 300px;
    height: 300px;
    margin: 0 auto 30px;
    text-align: center;
    @include cover;
    z-index: 2;
    @include sectionOverlay;
    @include flexColumn;
    justify-content: space-around;
    align-items: center;
    @include col();
    @include size(4);
    @include size-sm(6);

    @include respond-to(xs) {
        max-width: 100%;
        width: 100%;
        height: 261px;
        margin: 0 0 1px;
    }

    &:hover {

        &::before {
            background-color: rgba(#000, 0.65);
        }

        .blog-item {

            &__btn {
                opacity: 1;
                visibility: visible;
                transform: translateY(-10px);
            }

            &__title {
                transform: translateY(40px);
            }
        }
    }

    &__wrap {
        max-width: 960px;
        @include row-flex();
        margin: auto;
        @include respond-to(sm) {
            justify-content: center;
        }
    } //.blog-item__wrap

    &__title {
        font-family: $f-light;
        font-weight: 300;
        font-size: 24px;
        color: #fff;
        transform: translateY(70px);
        @include tr;
        @include respond-to(xs) {
            transform: translateY(0);
        }

        span {
            display: block;
            margin-top: 5px;
            font-family: $f-bold;
            font-size: 12px;
            text-transform: uppercase;
        }
    } //.blog-item__title

    &__btn {
        font-family: $f-light;
        border-width: 1px !important;
        opacity: 0;
        visibility: hidden;
        transform: translateY(-30px);
        @include respond-to(xs) {
            display: none;
        }
    } //.blog-item__btn
} //.blog-item
